    </div>
</template>
<script>
/*import AppButton from '../components/Button'*/
import axios from "axios";

export default {

    name: 'topHeader',

    components: {
        //AppButton,

    },

    showMenu: false,
    data: () => ({
        active_el: 0,
        drawer: false,
        group: null,
        items: [
            { title: 'Click Me' },
            { title: 'Click Me' },
            { title: 'Click Me' },
            { title: 'Click Me 2' },
        ],
        offset: true,
        istTime: "",
        temp: ""
    }),
    watch: {
        group() {
            this.drawer = false
        },
    },
    methods: {
        activate: function(el) {
            this.active_el = el;
        },
        handleclick() {
            console.log('click')
        },
        DisplayCurrentTime() {
            let date = new Date();
            let hours = date.getHours() > 12 ? date.getHours() - 12 : date.getHours();
            let am_pm = date.getHours() >= 12 ? "PM" : "AM";
            hours = hours < 10 ? "0" + hours : hours;
            let minutes = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
            this.istTime = hours + ":" + minutes + " " + am_pm;
            axios.get('https://api.openweathermap.org/data/2.5/weather?q=chennai&APPID=697abcdda0d56c5ed47f43367d73a59c')
                .then((tempData) => {

                    this.temp = tempData.data.main.temp - 273.15;
                    this.temp = parseFloat(this.temp).toFixed(2)

                })

        },

    },
    mounted() {
        console.log(this.$vuetify.breakpoint.width)
        this.DisplayCurrentTime();
    },


}
</script>
